"use client";

import { useState } from "react";
import {
  MdPerson,
  MdEdit,
  MdSave,
  MdClose,
  MdNotifications,
  MdPalette,
  MdSecurity,
  MdLogout,
  MdArrowBack,
} from "react-icons/md";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Switch } from "@/components/ui/switch";
import { Separator } from "@/components/ui/separator";
import { Skeleton } from "@/components/ui/skeleton";
import { useTabStore } from "@/hooks/use-tab-store";

interface UserProfile {
  id: string;
  name: string | null;
  avatar: string | null;
  bio: string | null;
  email: string | null;
  createdAt: string;
}

interface SettingsTabProps {
  experienceId?: string;
  user: UserProfile | null;
  isLoading?: boolean;
  standalone?: boolean;
}

export function SettingsTab({
  experienceId,
  user,
  isLoading,
  standalone,
}: SettingsTabProps) {
  const { setTab } = useTabStore();
  const [isEditing, setIsEditing] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [formData, setFormData] = useState({
    name: user?.name || "",
    bio: user?.bio || "",
  });

  // Notification preferences (local state for now)
  const [notifications, setNotifications] = useState({
    sessionReminders: true,
    newRequests: true,
    marketingEmails: false,
  });

  if (isLoading || !user) {
    return <SettingsLoading />;
  }

  const handleSave = async () => {
    setIsSaving(true);
    try {
      const response = await fetch("/api/users/profile", {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });

      if (response.ok) {
        setIsEditing(false);
        // Refresh the page to show updated data
        window.location.reload();
      }
    } catch (error) {
      console.error("Failed to save profile:", error);
    } finally {
      setIsSaving(false);
    }
  };

  const handleCancel = () => {
    setFormData({
      name: user.name || "",
      bio: user.bio || "",
    });
    setIsEditing(false);
  };

  return (
    <div className="p-4 sm:p-6 md:p-8 max-w-3xl mx-auto">
      {/* Back Button (standalone mode) */}
      {standalone && (
        <button
          onClick={() => setTab("profile")}
          className="inline-flex items-center gap-2 text-muted-foreground hover:text-foreground mb-6 transition-colors"
        >
          <MdArrowBack className="h-4 w-4" />
          <span className="text-sm">Back to Profile</span>
        </button>
      )}

      {/* Header */}
      <div className="mb-6">
        <h1 className="text-2xl font-semibold text-foreground">Settings</h1>
        <p className="text-muted-foreground mt-1">
          Manage your account and preferences
        </p>
      </div>

      {/* Profile Section */}
      <Card className="mb-6">
        <CardHeader>
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="h-10 w-10 rounded-full bg-primary/10 flex items-center justify-center">
                <MdPerson className="h-5 w-5 text-primary" />
              </div>
              <div>
                <CardTitle className="text-lg">Profile</CardTitle>
                <CardDescription>Your public profile information</CardDescription>
              </div>
            </div>
            {!isEditing ? (
              <Button
                variant="outline"
                size="sm"
                onClick={() => setIsEditing(true)}
                className="gap-2"
              >
                <MdEdit className="h-4 w-4" />
                Edit
              </Button>
            ) : (
              <div className="flex gap-2">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleCancel}
                  className="gap-2"
                >
                  <MdClose className="h-4 w-4" />
                  Cancel
                </Button>
                <Button
                  size="sm"
                  onClick={handleSave}
                  disabled={isSaving}
                  className="gap-2"
                >
                  <MdSave className="h-4 w-4" />
                  {isSaving ? "Saving..." : "Save"}
                </Button>
              </div>
            )}
          </div>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Avatar */}
          <div className="flex items-center gap-4">
            <Avatar className="h-16 w-16">
              <AvatarImage src={user.avatar || undefined} />
              <AvatarFallback className="text-xl">
                {user.name?.[0]?.toUpperCase() || "?"}
              </AvatarFallback>
            </Avatar>
            <div>
              <p className="text-sm text-muted-foreground">
                Your profile picture is synced from your Whop account
              </p>
            </div>
          </div>

          <Separator />

          {/* Name */}
          <div className="space-y-2">
            <Label htmlFor="name">Display Name</Label>
            {isEditing ? (
              <Input
                id="name"
                value={formData.name}
                onChange={(e) =>
                  setFormData({ ...formData, name: e.target.value })
                }
                placeholder="Enter your name"
              />
            ) : (
              <p className="text-foreground">{user.name || "Not set"}</p>
            )}
            <p className="text-xs text-muted-foreground">
              This name will be displayed to other users
            </p>
          </div>

          {/* Bio */}
          <div className="space-y-2">
            <Label htmlFor="bio">Bio</Label>
            {isEditing ? (
              <Textarea
                id="bio"
                value={formData.bio}
                onChange={(e) =>
                  setFormData({ ...formData, bio: e.target.value })
                }
                placeholder="Tell others about yourself..."
                rows={3}
              />
            ) : (
              <p className="text-foreground">
                {user.bio || "No bio added yet"}
              </p>
            )}
          </div>

          {/* Email (read-only) */}
          {user.email && (
            <div className="space-y-2">
              <Label>Email</Label>
              <p className="text-foreground">{user.email}</p>
              <p className="text-xs text-muted-foreground">
                Managed through your Whop account
              </p>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Notifications Section */}
      <Card className="mb-6">
        <CardHeader>
          <div className="flex items-center gap-3">
            <div className="h-10 w-10 rounded-full bg-primary/10 flex items-center justify-center">
              <MdNotifications className="h-5 w-5 text-primary" />
            </div>
            <div>
              <CardTitle className="text-lg">Notifications</CardTitle>
              <CardDescription>
                Choose what notifications you receive
              </CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex items-center justify-between">
            <div>
              <p className="font-medium text-foreground">Session Reminders</p>
              <p className="text-sm text-muted-foreground">
                Get notified before your scheduled sessions
              </p>
            </div>
            <Switch
              checked={notifications.sessionReminders}
              onCheckedChange={(checked) =>
                setNotifications({ ...notifications, sessionReminders: checked })
              }
            />
          </div>

          <Separator />

          <div className="flex items-center justify-between">
            <div>
              <p className="font-medium text-foreground">New Requests</p>
              <p className="text-sm text-muted-foreground">
                Get notified when someone requests a session
              </p>
            </div>
            <Switch
              checked={notifications.newRequests}
              onCheckedChange={(checked) =>
                setNotifications({ ...notifications, newRequests: checked })
              }
            />
          </div>

          <Separator />

          <div className="flex items-center justify-between">
            <div>
              <p className="font-medium text-foreground">Marketing Emails</p>
              <p className="text-sm text-muted-foreground">
                Receive updates about new features and tips
              </p>
            </div>
            <Switch
              checked={notifications.marketingEmails}
              onCheckedChange={(checked) =>
                setNotifications({ ...notifications, marketingEmails: checked })
              }
            />
          </div>
        </CardContent>
      </Card>

      {/* Account Section */}
      <Card>
        <CardHeader>
          <div className="flex items-center gap-3">
            <div className="h-10 w-10 rounded-full bg-muted flex items-center justify-center">
              <MdSecurity className="h-5 w-5 text-muted-foreground" />
            </div>
            <div>
              <CardTitle className="text-lg">Account</CardTitle>
              <CardDescription>Account information and actions</CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <p className="text-sm text-muted-foreground">Member since</p>
            <p className="font-medium text-foreground">
              {new Date(user.createdAt).toLocaleDateString("en-US", {
                month: "long",
                day: "numeric",
                year: "numeric",
              })}
            </p>
          </div>

          <Separator />

          <div className="pt-2">
            {standalone ? (
              <p className="text-sm text-muted-foreground">
                To change your password or delete your account, please contact support.
              </p>
            ) : (
              <>
                <p className="text-sm text-muted-foreground mb-3">
                  Your account is managed through Whop. To change your password or
                  delete your account, please visit your Whop settings.
                </p>
                <Button variant="outline" className="gap-2" asChild>
                  <a
                    href="https://whop.com/settings"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <MdLogout className="h-4 w-4" />
                    Manage on Whop
                  </a>
                </Button>
              </>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}

function SettingsLoading() {
  return (
    <div className="p-4 sm:p-6 md:p-8 max-w-3xl mx-auto">
      <div className="mb-6">
        <Skeleton className="h-8 w-32" />
        <Skeleton className="h-4 w-64 mt-2" />
      </div>

      <Card className="mb-6">
        <CardContent className="p-6 space-y-6">
          <div className="flex items-center gap-4">
            <Skeleton className="h-16 w-16 rounded-full" />
            <Skeleton className="h-4 w-48" />
          </div>
          <Skeleton className="h-10 w-full" />
          <Skeleton className="h-24 w-full" />
        </CardContent>
      </Card>

      <Card>
        <CardContent className="p-6 space-y-4">
          {[...Array(3)].map((_, i) => (
            <div key={i} className="flex items-center justify-between">
              <Skeleton className="h-12 w-48" />
              <Skeleton className="h-6 w-12 rounded-full" />
            </div>
          ))}
        </CardContent>
      </Card>
    </div>
  );
}
